import { red } from '@material-ui/core/colors';
import React, { Component } from 'react';
import './Key.css';

const activeStyle = {
    top: 'calc(4.9vw)'
};


const inactiveStyle = {
    top:''
};

class Key extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            keyStyle: inactiveStyle 
        };
        this.handleClick = this.handleClick.bind(this);
        this.activateKey = this.activateKey.bind(this);
        this.playSound = this.playSound.bind(this);
    }
    
    activateKey() {
      
            if (this.state.keyStyle.top === 'calc(4.9vw)') {
                this.setState({
                    keyStyle: inactiveStyle
                });

            } else {
                this.setState({
                    keyStyle: activeStyle
                });

            }
      
    }
    playSound(target){
        this.sound = new Audio();
        this.sound.src = 'https://pianonotesforuse.s3.us-east-2.amazonaws.com/' + this.props.note+ '.mp3';
        this.sound.load();
        const promise = this.sound.play();
        if (promise !== undefined) { // On older browsers play() does not return anything, so the value would be undefined.
            promise
                .then(() => {
                    // Audio is playing.
                })
                .catch(error => {
                    console.log(error);
                });
        }

        this.activateKey();
        setTimeout(() => this.activateKey(), 100);

    }
 

    keyToPosition(noteName) {
        var groupOffset;
        var offset; 
    /* If 3 digits then black, if 2 digits then white; */
    if (noteName.length === 2) {
        groupOffset = parseInt(noteName.substring(1)) * 7 * 1.4 + 6;
        /* parse for white */
        switch(noteName.substring(0, 1)){
            case 'A':
                offset = groupOffset + 7.0; 
                break;
            case 'B':
                offset = groupOffset + 8.4; 
                break;
            case 'C':
                offset = groupOffset; 
                break;
            case 'D':
                offset = groupOffset + 1.4;
                break;
            case 'E':
                offset = groupOffset + 2.8; 
                break; 
            case 'F':
                offset = groupOffset + 4.2; 
                break;
            case 'G':
                offset = groupOffset + 5.6;
                break;
            default:
                offset = 0; 
        }
        return 'calc(' + offset + 'vw)';
    } else {
        groupOffset = parseInt(noteName.substring(2)) * 7 * 1.4 + 6;
        /* parse for black */
        switch (noteName.substring(0, 1)) {
            case 'D':
                offset = groupOffset + 0.9;
                break;
            case 'E':
                offset = groupOffset + 2.3;
                break;
            case 'G':
                offset = groupOffset + 5.1;
                break;
            case 'A':
                offset = groupOffset + 6.6;
                break;
            case 'B':
                offset = groupOffset + 8.0;
                break;
            default:
                offset = 0;
        }
        return 'calc(' + offset + 'vw)';
    }

}  
    keyToGroupColor(noteName){
        if(this.props.octave){
      const colors = ['rgb(142,142,142)', 'rgb(225,178,73)', 'rgb(122,199,70)', 'rgb(82,189,136)', 'rgb(81,132,214)', 'rgb(122,66,216)', 'rgb(191,47,141)','rgb(216,73,66)', 'rgb(142,142,142)'];
      const groupNum = parseInt(noteName.length===2?noteName.substring(1):noteName.substring(2));
      if( noteName ==='C4'){
          if( this.props.middlec){
              return '#ffffff';
          }
      }
      return colors[groupNum];
     } else {
         if(noteName==='C4' && this.props.middlec){
             return '#ff0000';
         }
         return '#ffffff';
     } 
    }

    handleClick(e){
        this.playSound();
    }
    
    render() {
        var noteName = this.props.note;
        var leftOffset = this.keyToPosition(noteName);
        var keyColor = this.keyToGroupColor(noteName);
        var classInfo = (this.props.keyType === 'Whitekey')?'white_key key':'black_key key'; 
        var noteText = '';
        if(this.props.keyType ==='Whitekey'){
            noteText = noteName.substring(0, 1);
        } else {
            switch (noteName.substring(0, 1)){
               case 'D':
                    noteText = 'C♯ D♭';
                   break;
               case 'E':
                    noteText = 'D♯ E♭';
                   break;
               case 'G':
                    noteText = 'F♯ G♭';
                   break;
               case 'A':
                    noteText = 'G♯ A♭';
                   break;
               case 'B':
                    noteText = 'A♯ B♭';
                   break;
            default:
                noteText = '';
            }
        }

        var styleObj = { left: leftOffset };
         
        if (this.props.keyType === 'Whitekey'){
            styleObj = {left:leftOffset, backgroundColor: keyColor};
        }
        if(this.props.keynote){

            return (
            <div className={classInfo} onClick={this.handleClick}
                    note={noteName} style={styleObj}
            >
                <p>{noteText}</p>
            </div>
            );
        
        }
        else {
            return (
                <div className={classInfo} onClick={this.handleClick}
                    note={noteName} style={styleObj}
                >
                </div>
            );
        }
        
    } //end render()

}



export default Key; 




    
