import React from 'react';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import { withStyles } from '@material-ui/core/styles';
import { purple} from '@material-ui/core/colors';
import './ControlPanel.css'

const PurpleSwitch = withStyles({
    switchBase: {
        color: "#fafafa",
        '&$checked': {
            color: purple[500],
        },
        '&$checked + $track': {
            backgroundColor: purple[500],
        },
    },
    checked: {},
    track: {},
})(Switch);

class ControlPanel extends React.Component {
    
    constructor(props){
        super(props);
        this.state = {
            keynote: true,
            octave: true,
            middlec: false
        };
        this.handleChange = this.handleChange.bind(this);
    }
   
    handleChange = (event) => {
        this.setState({ ...this.state, [event.target.name]: event.target.checked });
        this.props.methodfromparent({ ...this.state, [event.target.name]: event.target.checked });
    };

    render(){
        return (
            <div className='controlpanel'>
                <FormGroup row>
                    <FormControlLabel
                        control={<Switch checked={this.state.keynote} onChange={this.handleChange} name="keynote" />}
                        label="Key Note"
                    />
                    <FormControlLabel
                        control={
                            <Switch
                                checked={this.state.octave}
                                onChange={this.handleChange}
                                name="octave"
                                color="primary"
                            />
                        }
                        label="Octave Group"
                    />
                <FormControlLabel
                    control={<PurpleSwitch checked={this.state.middlec} onChange={this.handleChange} name="middlec" />}
                    label="Middle C"
                />
                </FormGroup>
          </div>
            
        );
    }
}

export default ControlPanel; 