class Piano extends React.Component {
    constructor(props){
        super(props); 
        this.state = {
            keynote: true,
            octave: true,
            middlec: false
        };
        this.parentmethod = this.parentmethod.bind(this);
    }
    
    parentmethod(statedata) {
        this.setState(statedata);
    }

    render() {
        return (
            <div>
                <ControlPanel methodfromparent={this.parentmethod}/>
            <div className='piano' >
                    {keyboard.map((ele, index) => (
                        <Key key={ele.keyNote} keyType={ele.keyType} note={ele.keyNote} keynote={this.state.keynote} octave={this.state.octave} middlec={this.state.middlec} /> 
                    ))}
            </div>
            </div>
        );
    }
}

export default Piano; 



 